body {
    background-color: #111;
    color: #eee;
    font-family: "Clear Sans", "Helvetica Nue", Arial, Helvetica, sans-serif;
}

button {
    color: #ccc;
    width: 160px;
}

button:hover {
    color: #fff;
    background-color: #333;
}

h1 {
    border-bottom: 1px solid #eee;
    font-size: 3em;
    font-weight: 700;
    letter-spacing: 3.2px;
    margin: .5em auto 0 auto;
    text-align: center;
    text-transform: uppercase;
    width: 50%;
}

nav {
    margin-bottom: 3em;
    text-align: center;
}

textarea {
    font-size: 2em;
}

.left-col {
    display: inline-block;
    text-align: center;
    width: 180px;
    vertical-align: top;
}

.right-col {
    display: inline-block;
    text-align: center;
    vertical-align: top;
    width: 800px;
}

.input-label {
    font-size: 1.2em;
    margin-bottom: .8em;
    text-align: center;
    text-transform: uppercase;
    width: 160px;
}

.main {
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    width: 90%;
}

.wordle-input {
    height: 340px;
    width: 160px;
}